body {
  font-family: "Avenir", sans-serif; }

.main {
  display: flex;
  justify-content: center; }
  .main .container {
    width: 900px; }
    .main .container .apartment {
      margin-bottom: 44.05px;
      width: 450px; }
      .main .container .apartment__title {
        font-size: 36px;
        line-height: 42px;
        color: #3d4e61;
        margin-bottom: 10.16px; }
      .main .container .apartment .entire-apartament__link {
        display: block;
        font-size: 16px;
        line-height: 19px;
        color: #687c94;
        margin-bottom: 15px; }
      .main .container .apartment .apartment-specification {
        display: flex;
        margin-bottom: 28.05px; }
        .main .container .apartment .apartment-specification__link {
          font-size: 16px;
          line-height: 19px;
          color: #3d4e61;
          margin-right: 32px; }
          .main .container .apartment .apartment-specification__link:hover {
            color: #687c94; }
      .main .container .apartment__text {
        font-size: 16px;
        line-height: 24px;
        color: #3d4e61;
        margin-bottom: 24px; }
      .main .container .apartment__contact {
        font-size: 16px;
        line-height: 19px;
        color: #3d4e61;
        font-weight: bold; }
        .main .container .apartment__contact:hover {
          box-shadow: 0 1px 0 #3d4e61; }
    .main .container .amenities {
      margin-bottom: 66.05px; }
      .main .container .amenities__title {
        font-size: 20px;
        line-height: 23px;
        color: #3d4e61;
        margin-bottom: 25px;
        font-weight: bold; }
      .main .container .amenities .amenities-item {
        width: 400px;
        display: flex;
        flex-wrap: wrap; }
        .main .container .amenities .amenities-item__link {
          font-size: 16px;
          line-height: 19px;
          color: #3d4e61;
          margin-bottom: 17px;
          flex: 0 0 175px; }
          .main .container .amenities .amenities-item__link:hover {
            color: #687c94; }
      .main .container .amenities .all-amenities__link {
        font-size: 16px;
        line-height: 19px;
        color: #3d4e61;
        font-weight: bold; }
        .main .container .amenities .all-amenities__link:hover {
          box-shadow: 0 1px 0 #3d4e61; }
    .main .container .reviews {
      margin-bottom: 50px;
      width: 500px; }
      .main .container .reviews__title {
        font-size: 28px;
        line-height: 33px;
        color: #3d4e61;
        font-weight: bold;
        margin-bottom: 38px; }
      .main .container .reviews__persons {
        margin-bottom: 20px; }
        .main .container .reviews__persons .reviews-person {
          display: flex;
          align-items: center;
          margin-bottom: 23px; }
          .main .container .reviews__persons .reviews-person__photo {
            margin-right: 21px;
            height: 50px; }
          .main .container .reviews__persons .reviews-person__info {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            height: 50px; }
            .main .container .reviews__persons .reviews-person__info__name {
              font-size: 20px;
              color: #3d4e61; }
            .main .container .reviews__persons .reviews-person__info__data {
              font-size: 14px;
              color: #3d4e61; }
      .main .container .reviews__text {
        font-size: 16px;
        line-height: 19px;
        color: #3d4e61; }
